import React, { useState,useEffect } from 'react'

const Carosol = ({pics}) => {
 const[index,setIndex]=useState(0);/* eslint no-unused-vars : "off" */
 const[img,setImg]=useState(pics);
 const[left,setLeft]=useState(true);
 const [right,setRight]=useState(true);
 /* eslint no-unused-vars : "off" */

  const setIndexVal=()=>{
  if(index<img.length-1)
  {
   setIndex(index+1);
  }
  else{
   setIndex(0);
  }
 }


 useEffect(() => {
   const timer = setTimeout(()=>{
   setIndexVal();
  },5000)
  return ()=>clearTimeout(timer);
 }, [index])// eslint-disable-line react-hooks/exhaustive-deps
 


 const handleLeftClick=()=>{
   if(index !== 0)
   {
     setIndex(index-1);
   }
}

  const handleRightClick=()=>{
   if(index!==img.length-1)
   {
      setIndex(index+1);
   }
 }


 

  return (
    <div className="img-hold" >
     <div onClick={handleLeftClick} className="left">
       { (index !== 0) 
        &&
       <div className="ml-c">
         <i class="fas fa-chevron-left " style={{color:"#000"}}></i>
       </div>
       }
       
     </div>
     <div onClick={handleRightClick} className="right">
       {(index!==img.length-1)
        && 
       <div className="mr-c">
        <i class="fas fa-chevron-right " style={{color:"#000"}}></i>
       </div>
       }
       
     </div>
      <img className="projectpics" src={img[index]} alt="" />
    </div>
  )
} 


export default Carosol
